import { React, useEffect, useState } from 'react';
import { Link, useParams } from 'react-router-dom';
import { PieChart } from 'react-minimal-pie-chart';
import { MatchSmallCard } from '../components/MatchSmallcard';
import { MatchDetailCard } from '../components/MatchDetailCard';
import './TeamPage.scss';

export const TeamPage = () => {

	const [team, setTeam] = useState({ matches: [] });
	const { teamName } = useParams();
	useEffect(
		() => {
			const fetchTeams = async () => {
				const response = await fetch(`${process.env.REACT_APP_API_ROOT_URL}/team/${teamName}`);
				const data = await response.json();
				setTeam(data);
			};
			fetchTeams();
		}, [teamName]
	);
	if (!team || !team.teamName) {
		return <h1>Team not found</h1>
	}
	return (


		<div className="TeamPage">
			<div className="home-link">
				<Link to='/teams'>Home</Link>
			</div>


			<div className="team-name-section">
				<h1 className="team-name">{team.teamName}</h1>
			</div>
			<div className="win-loss-section">Wins / Losses
			<PieChart
					data={[
						{ title: 'Loses', value: team.totalMatches - team.totalWins, color: '#a34d5d' },
						{ title: 'Wins', value: team.totalWins, color: '#4da375' }

					]}
				/>
			</div>
			<div className="match-detail-section">
				<h3>Latest matches</h3>
				<MatchDetailCard teamName={team.teamName} match={team.matches[0]} />
			</div>
			{team.matches.slice(1).map(match => <MatchSmallCard key={match.id} teamName={team.teamName} match={match} />)}

			<div className="more-link">
				<Link to={`/teams/${teamName}/matches/${process.env.REACT_APP_DATA_END_YEAR}`}>	More &gt;</Link>
			</div>

		</div>
	);
}